body {
  margin: 0;
  font-family: Bodoni-72;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Consolas, "Courier New", monospace;
}

:root {
  --prussian-blue: hsla(209, 53%, 16%, 1);
  --sapphire-blue: hsla(199, 100%, 29%, 1);
  --celadon-blue: hsla(198, 63%, 38%, 1);
  --carolina-blue: hsla(202, 78%, 49%, 1);
  --azure-x-11-web-color: hsla(186, 28%, 93%, 1);
}

* {
  background: var(--carolina-blue);
}

a {
  background: none;
}

p {
  font-size: 18px;
  color: var(--azure-x-11-web-color);
  font-weight: bold;
  background-color: white;
  text-align: center;
}
.error {
  color: red;
}

h1 {
  margin: auto 30px;
  padding: 10px;
  color: var(--azure-x-11-web-color);
  font-weight: bold;
}

#header {
  display: flex;
  margin-left: 5px;
}
.topic {
  margin: 0;
  padding-right: 1080px;
}

.aboutMe {
  width: 500px;
  height: 500px;
}

.links {
  text-decoration: none;
}

#navigation {
  align-items: flex-end;
  font-size: 22px;
  text-decoration: none;
  align-content: center;
}

#navigation a {
  color: var(--prussian-blue);
  padding-left: 1em;
  font-weight: bold;
  text-decoration: none;
  text-shadow: 1px 1px 2px var(--onyx);
}

a:visited {
  background-color: whitesmoke;
}
a:focus {
  background-color: whitesmoke;
}
a:active {
  background-color: whitesmoke;
}
a:focus {
  background-color: whitesmoke;
}
.card {
  background-color: var(--platinum);
  border-radius: 5px;
  height: 400px;
  margin: 2rem;
  position: relative;
  width: 400px;
  box-shadow: 0 2px 2px var(--onyx), 0 6px 12px var(--onyx);
}

.card .img-container {
  height: 75%;
  overflow: hidden;
  text-align: center;
  border-radius: 10px;
  background-color: var(--platinum);
}

.card .img-container img {
  width: 100%;
  padding: 25px;
  margin-bottom: 20px;
  background-color: var(--platinum);
}

.card .content {
  font-size: 18px;
  background-color: var(--platinum);
  color: var(--orange-soda);
  font-weight: bold;
  text-align: center;
}

#project-icon {
  background-color: var(--platinum);
  padding: 5px;
}

#topics {
  font-size: small;
  font-style: italic;
  color: var(--onyx);
}

#card-title {
  margin-top: 10px;
  color: var(--onyx);
  text-shadow: 1px 1px 6px var(--sandy-brown);
}

.wrapper {
  padding-top: 50px;
  justify-content: space-around;
  height: 100%;
  display: flex;
  overflow: auto;
  flex-flow: row wrap;
  padding: 10px;
  align-content: flex-start;
}

#footer {
  padding: 5px;
  margin-top: 10px;
}

.icon {
  padding: 10px;
}

#about-section {
  margin: 2em;
}

#about-section p {
  background-color: var(--alabaster);
  color: var(--onyx);
}

#contact-section {
  justify-content: center;
  margin: 20px;
  margin-left: 10em;
  margin-right: 10em;
}

#front-back ul {
  list-style-type: none;
}

#front-back ul li {
  list-style-type: none;
}

#front-back h2 {
  color: var(--onyx);
}

#front-back p {
  background-color: var(--alabaster);
}

#resume p {
  background-color: var(--alabaster);
  color: var(--onyx);
  margin-top: 5px;
}

#footer {
  background-color: var(--onyx);
}

#footer .icon {
  background-color: var(--onyx);
  margin-top: 20px;
}

#footer p {
  background-color: var(--onyx);
  margin-top: 8px;
}

#footer a {
  text-decoration: none;
  color: var(--orange-soda);
}

#react-icon {
  background-color: var(--onyx);
}
